import React from 'react'
// import {Form,FormLabel} from 'react-bootstrap'
import { debounce } from 'lodash';

function SearchBar({setEpisodes, setCurrentPage, setFilterAPIText}){
    
    const [searchText, setSearchText] = React.useState('');
    const handleChange = function(event){
  
      setSearchText(event.target.value)
      

    }
    const debouncedAPICall = React.useCallback(
      debounce((filterText)=>{
        console.log("search called", filterText)
      let pageURL = 'https://rickandmortyapi.com/api/episode/?name=' + filterText
      fetch(pageURL)
      .then(res => res.json())
      .then(
        (result) => {
          if(result["error"]){
            setEpisodes({
              isLoaded: true,
              error: result["error"]
            })
          }
          else{
          let episodeArray = result["results"]
          episodeArray.map( x=> { return {
            isDone : true,
            ...x
  
          }})
          console.log("set Episode called")
          setCurrentPage(1)
          setFilterAPIText(filterText)
          setEpisodes({
            isLoaded: true,
            items: episodeArray,
            totalPages: result["info"]["pages"]
          });
        }
        },
        
        (error) => {
          setEpisodes({
            isLoaded: true,
            error
          });
        }
      )
    }
      , 300),
      []
    );
  
    React.useEffect(()=>{
     console.log("use effect called") 
     debouncedAPICall(searchText)
    },[searchText,debouncedAPICall])

    return (
      <>
        <div>
            <label htmlFor="searchText" > Episode Search </label><span>{" "}</span>
            <input value={searchText} onChange={handleChange} id="searchText" />
        </div>
       
      </>
    )
  }

  export default SearchBar