import React from "react";
import "./App.css";
import { Card,Modal,Button, Table} from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import Pagination from './Pagination'
import Episode from './Episode'
import SearchBar from './SearchBar'
import CardHeader from "react-bootstrap/esm/CardHeader";



function App() {
  const [episodes, setEpisodes] = React.useState([
    {
      error: null,
      isLoaded: false,
      items: []
    }
  ]);

  const [currentPage, setCurrentPage] = React.useState(1);
  //const [searchText, setSearchText] = React.useState('');
  const [modalState, setModalState] = React.useState({
    show : false,
    deleteId: null
  })
  const [filterAPIText, setFilterAPIText] = React.useState('')
  const [deletedEpisodeIDs, setDeletedEpisodeIDs] = React.useState([0]);
  const [watchedEpisodeIDs, setWatchedEpisodeIDs] = React.useState([0]);
  const [sortConfig, setSortConfig] = React.useState(null)

  
  // const regex = new RegExp(`(.*)${searchText.trim()}(.*)`, 'i');
  // const filteredEpisodes = episodes.items? episodes.items.filter(({name}) => name.match(regex)):[];
  //const rowsPerPage = 5;
  //const count = filteredEpisodes?filteredEpisodes.length:0;
  //const totalPages = Math.ceil(count / rowsPerPage)
  
  
    const filteredEpisodes = episodes.items? episodes.items.filter(function(x)
  { 
    //console.log(x)
    return deletedEpisodeIDs.findIndex(y=>y===x.id) === -1
  }): []

   const Rows = filteredEpisodes? filteredEpisodes.map(x => {return {
    ...x,
    isDone : watchedEpisodeIDs.findIndex(y=>y===x.id) === -1 ?false:true 

    }}) :[]
  
  //calculatedRows.current = Rows?getSortedRows(Rows):[]
  

  
  
    
    const getSortedRows = React.useCallback((Rows)=>{
      if(!Rows)
      return
      
      if(sortConfig){
      console.log(sortConfig)
      let sortedProducts = [...Rows]
      sortedProducts.sort((a, b) => {
        if (a[sortConfig.key] < b[sortConfig.key]) {
          return sortConfig.direction === 'ascending' ? -1 : 1;
        }
        if (a[sortConfig.key] > b[sortConfig.key]) {
          return sortConfig.direction === 'ascending' ? 1 : -1;
        }
        return 0;
      });
      return sortedProducts
       }
       else 
       return Rows
     },[sortConfig])

     const calculatedRows = getSortedRows(Rows)
     console.log("sortedObject",calculatedRows)

    
    
  


  React.useEffect(()=>{
    let pageURL = 'https://rickandmortyapi.com/api/episode/?page=' + currentPage + '&name=' + filterAPIText
    fetch(pageURL)
    .then(res => res.json())
    .then(
      (result) => {
        if(result["error"]){
          setEpisodes({
            isLoaded: true,
            error: result["error"]
          })
        }
        else{
        let episodeArray = result["results"]
        episodeArray.map( x=> { return {
          isDone : true,
          ...x

        }})
        setEpisodes({
          isLoaded: true,
          items: episodeArray,
          totalPages: result["info"]["pages"]
        });
      }
      },
      
      (error) => {
        setEpisodes({
          isLoaded: true,
          error
        });
      }
    )
  },[currentPage,filterAPIText])

 

  const markWatched = index => {
    const newepisodes = [...(episodes.items)];
    newepisodes.find(x => x.id===index).isDone = true;
    const newObj = {
      ...episodes,
      items: newepisodes
    }
    setEpisodes(newObj);
    setWatchedEpisodeIDs(
      [...watchedEpisodeIDs,
      index
      ]
    )
  };

  const removeEpisodeModalTrigger = function(id){ 
    setModalState({
      show: true,
      deleteId: id
    })
  };

  
  const handleModalClose = () => setModalState({
    ...modalState,
    show: false
  });

  const handleConfirmDelete = id => {
    const newepisodes = [...episodes.items];
    let index = newepisodes.findIndex(x => x.id===id)
    newepisodes.splice(index, 1);
    const newObj = {
      ...episodes,
      items: newepisodes
    }
    setEpisodes(newObj);
    setDeletedEpisodeIDs([
      ...deletedEpisodeIDs,
      id
    ])
    setModalState({
      show: false,
      deleteId: null
    })
  };

  const requestSort = key => {
    let direction = 'ascending';
    if(sortConfig){
    if (sortConfig.key === key && sortConfig.direction === 'ascending') {
      direction = 'descending';
    }
    }
    setSortConfig({ key, direction });
  }

  let toDeleteIndex = episodes.items?episodes.items.findIndex(x=> x.id === modalState.deleteId):null;
  if(!episodes.isLoaded){
    return (
      <div>
        <Card>Loading the data, please be patient...</Card>
      </div>
    )
  }

  else{
  return (
    <div className="app">
      <div className="container">
        <h1 className="text-center mb-4">Episode List</h1>
        <SearchBar setCurrentPage={setCurrentPage} setEpisodes={setEpisodes} setFilterAPIText={setFilterAPIText} ></SearchBar>
        <br></br>
        <Modal show={modalState.show} onHide={handleModalClose}>
        <Modal.Header closeButton>
          <Modal.Title>Confirm Delete</Modal.Title>
        </Modal.Header>
        
        <Modal.Body>Are you sure you want to delete {episodes.items && toDeleteIndex>=0 ? episodes.items[toDeleteIndex].episode + " " + episodes.items[toDeleteIndex].name :''} from the list?</Modal.Body>
        
        <Modal.Footer>
          <Button variant="secondary" onClick={handleModalClose}>
            Cancel
          </Button>
          <Button variant="primary" onClick={()=> {handleConfirmDelete(modalState.deleteId)}}>
            Yes, I'm sure!
          </Button>
        </Modal.Footer>
      </Modal>
        {episodes.error !== undefined &&
        <div>
          <Card> <CardHeader>Error loading the data due to { " " + episodes.error}</CardHeader></Card>
        </div>
        }
        { calculatedRows && episodes.error === undefined &&
        <Table striped hover>
          <thead>
            <tr key={'heading'}>
              <th><Button variant="*" onClick={()=> {requestSort('episode')  } }>Episode Number</Button></th>
              <th><Button variant="*" onClick={()=> {requestSort('name')  } }>Name</Button></th>
              <th><Button variant="*" onClick={()=> {requestSort('isDone')  } }>Watched</Button></th>
              <th>Delete</th>
            </tr>
          </thead>
          <tbody>
          {calculatedRows.map((episodeObject) => (
            < tr className='episode' key={episodeObject.id}> 
              
                <Episode
                index={episodeObject.id}
                name={episodeObject.name}
                episode={episodeObject.episode}
                isDone={episodeObject.isDone}
                markWatched={markWatched}
                removeEpisodeModalTrigger={removeEpisodeModalTrigger}
                // confirmDelete={confirmDelete}
                />
              
            </tr>
          ))}
          </tbody>
        </Table>
        }
      <br></br>
      <Pagination
        currentPage={currentPage}
        // count={count}
        // rowsPerPage={rowsPerPage}
        totalPages={episodes.totalPages}
        setCurrentPage={setCurrentPage}
      />
      </div>
    </div>
  );
  }
}

export default App;