import { Button } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';

function Episode({ name, episode, isDone, index, markWatched, removeEpisodeModalTrigger }) {
    return (
      <>
        
            <td style={{ textDecoration: isDone ? "line-through" : "" }}> {episode}</td>
            <td style={{ textDecoration: isDone ? "line-through" : "" }}>{name}</td> 
            <td>
            <Button variant="outline-success" onClick={(e) => {
                e.target.blur()
                markWatched(index)
                }}>
                
            </Button>
            </td>
            <td>
            <Button variant="outline-danger" onClick={(e) => {
                e.target.blur()
                removeEpisodeModalTrigger(index)
                }}>
                
            </Button>
            </td>
        
      </>
    );
  }

  export default Episode