function Pagination({ currentPage, count, rowsPerPage, totalPages, setCurrentPage }){

   //const beginning = currentPage === 1 ? 1 : rowsPerPage * (currentPage - 1) + 1
    //const end = currentPage === totalPages ? count : beginning + rowsPerPage - 1
  
    return (
      <>
        <div className="pagination">
          <button disabled={currentPage === 1} onClick={() => setCurrentPage(1)}>
             First
          </button>
          <button disabled={currentPage === 1} onClick={() => setCurrentPage(currentPage=>currentPage - 1)}>
             Previous
          </button>
          <button disabled={currentPage === totalPages} onClick={() => setCurrentPage(currentPage=>currentPage + 1)}>
            Next 
          </button>
          <button disabled={currentPage === totalPages} onClick={() => setCurrentPage(totalPages)}>
            Last 
          </button>
        </div>
        <p>
          Page {currentPage} of {totalPages}
        </p>
        {/* <p>
          Rows: {beginning === end ? end : `${beginning} - ${end}`} of {count}
        </p> */}
      </>
    )
  }

  export default Pagination